@import "variables";
@import "mixins";

body{
  display: grid;
  grid-template-areas: "header"
                       "section-1"
                       "section-2";
  grid-template-columns: 1fr;
  font-size: 16px;
  .header{
    grid-area: header;
    .containerParent();
    align-items: center;
    height: 145px;
    border-bottom: 1px solid #000;
    .container{
      .containerChild();
      display: flex;
      justify-content: space-between;
      .header__name{
        width: 25%;
        text-align: center;
        h2 {
          font: 1.875em/2em @Montser;
        }
        span{
          font: 1em/1.125em @Montser;
        }
      }
      .header__center{
        display: flex;
        flex-direction: column;
        width: 50%;
        .info{
          ul{
            display: flex;
            justify-content: space-between;
            li{
              span{
                font: .875em/1em @OpenS;
                i{
                  color: #933ec5;
                }
              }
              a{
                font: 1.125em/1.25em @Montser;
                color: #000;
                text-decoration: none;
                &:hover{
                  text-decoration: underline;
                }
              }
            }
          }
        }
        .border{
          margin: 15px 0 10px;
          border-bottom: 1px solid #959595;
        }
        .nav-panel{
          ul{
            display: flex;
            justify-content: space-around;
            li{
              a{
                font: .875em/1em @RobotoB;
                color: #000;
                text-decoration: none;
                &:hover{
                  text-decoration: underline;
                }
              }
            }
          }
        }
      }
      .header__offer {
        width: 25%;
        text-align: center;
        button {
          width: 200px;
          font: .75em/3em @RobotoB;
          color: #533364;
          text-transform: uppercase;
          background: transparent;
          border: 1px solid #933ec5;
          border-radius: 50px;
          box-shadow: 5px 5px 25px #ebebeb;
          outline: none;
          cursor: pointer;
        }
      }
    }
  }
  .section1{
    grid-area: section-1;
    background-color: #ebebeb;
    .containerParent();
    .container{
      .containerChild();
      display: flex;
      justify-content: space-between;
      .appleRepair{
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 500px;
        width: 75%;
        margin-right: 25px;
        padding: 0 25px;
        background-color: #533364;
        border-radius: 50px;
        &__info {
          h4 {
            font: 2.625em/1.5em @Montser;
            color: #fff;
          }
          ul {
            display: flex;
            flex-wrap: wrap;
            li {
              width: 50%;
              margin: 5px 0;
              img {
                vertical-align: middle;
              }
              span {
                font: 1.125em/1.25em @RobotoB;
                color: #fff;
              }
            }
          }
        }
      }
      .sale{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 20%;
        min-height: 500px;
        padding: 0 25px;
        background-color: #eed1a0;
        border-radius: 50px;
        p{
          font: 1.125em/1.25em @RobotoB;
          color: #fff;
          text-align: center;
          span{
            font: 1.875em/2em @Montser;
          }
        }
      }
    }
  }
  .section2{
    grid-area: section-2;
    .containerParent();
    .container {
      .containerChild();
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      .aboutUs{
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 100%;
        min-height: 500px;
        border-bottom: 1px solid #ebebeb;
        .info{
          margin-right: 25px;
          h4{
            font: 2.25em/2.375em @Montser;
          }
          p{
            font: 1.25em/1.375em @RobotoB;
          }
          button{
            margin-top: 30px;
            width: 225px;
            height: 55px;
            font: .75em/.875em @RobotoB;
            color: #fff;
            text-transform: uppercase;
            background-color: #533364;
            border-radius: 50px;
            outline: none;
            cursor: pointer;
            box-shadow: 5px 5px 25px #ebebeb;
          }
        }
        .video{
          iframe{
            border-radius: 30px;
            box-shadow: 5px 5px 25px #ebebeb;
          }
        }
      }
      .bottom{
        width: 100%;
        padding: 25px 0;
        ul{
          display: flex;
          justify-content: space-around;
          li{
            p{
              font: 1.125em/1.25em @RobotoM;
            }
          }
        }
      }
    }
  }
}